<template>
    <div>
        <div class="select-dot">
            <i :class="activeIndex === i ? 'active' : ''" v-for="(v,i) in charts" :key="i" @mousemove="changeChart(i)"></i>
        </div>
        <div id="chart"></div>
    </div>
</template>

<script>
import {defineComponent, reactive, ref, getCurrentInstance, onMounted, onUnmounted} from 'vue';
import echarts from '@/assets/ts/echarts.ts';

export default defineComponent({
    name: "DataStatistics",
    setup() {
        const app = getCurrentInstance();
        const activeIndex = ref(0);
        const lineOption = {
            title: {
                text: '2021年收支情况',
                subtext: '纯属虚构',
                left: 'center'

            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['收入', '支出'],
                left: 'left'

            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月',]
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '收入',
                    type: 'line',
                    smooth: true,
                    data: [7200, 9800, 9600, 7400, 6800, 9500, 10010, 5400, 6022, 1544, 5900, 12001]
                },
                {
                    name: '支出',
                    type: 'line',
                    smooth: true,
                    data: [8000, 3500, 5200, 1500, 3200, 2500, 4000, 1800, 4700, 3000, 3500, 1400]
                },
            ]
        };
        const pieOption = {
            title: {
                text: '某站点用户访问来源',
                subtext: '纯属虚构',
                left: 'center'
            },
            tooltip: {
                trigger: 'item'
            },
            legend: {
                orient: 'vertical',
                left: 'left',
            },
            series: [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius: '50%',
                    data: [
                        {value: 1048, name: '搜索引擎'},
                        {value: 735, name: '直接访问'},
                        {value: 580, name: '邮件营销'},
                        {value: 484, name: '联盟广告'},
                        {value: 300, name: '视频广告'}
                    ],
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        }
        const charts = reactive([
            {
                type: 'line',
                option: lineOption
            },
            {
                type: 'pie',
                option: pieOption
            }
        ]);
        let chart = reactive();
        const initChart = ()=> {
            chart.dispose();
            chart = echarts.init(document.getElementById('chart'));
            chart.setOption(charts[activeIndex.value].option)
        }
        const changeChart = (i) => {
            if (activeIndex.value === i) return ;
            activeIndex.value = i;
            initChart();
        }
        onMounted(() => {
            chart = echarts.init(document.getElementById('chart'));
            initChart();
        })
        onUnmounted(() => {
            chart.dispose();
        })
        return {
            initChart,
            changeChart,
            charts,
            activeIndex,
            chart
        }
    }
})
</script>

<style scoped lang="less">
#chart {
    width: 1100px;
    height: 331px;
}
.select-dot {
    text-align: right;
    i {
        display: inline-block;
        width: 12px;
        height: 12px;
        background: #ddd;
        border-radius: 50%;
        margin-right: 5px;

    }
    .active {
        background: #aaa;
    }
}
</style>
